import * as React from "react";
import PropTypes from "prop-types";
import Box from "@mui/material/Box";
import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";
import CardMedia from "@mui/material/CardMedia";
import Typography from "@mui/material/Typography";
import { CardActionArea } from "@mui/material";
import menu from "./json/gaiyou";

function Item(props) {
  const { sx, ...other } = props;
  return (
    <Box
      sx={{
        p: 1,
        m: 1,
        bgcolor: (theme) =>
          theme.palette.mode === "dark" ? "#101010" : "grey.100",
        color: (theme) =>
          theme.palette.mode === "dark" ? "grey.300" : "grey.800",
        border: "1px solid",
        borderColor: (theme) =>
          theme.palette.mode === "dark" ? "grey.800" : "grey.300",
        borderRadius: 2,
        fontSize: "0.875rem",
        fontWeight: "700",
        ...sx
      }}
      {...other}
    />
  );
}

Item.propTypes = {
  /**
   * The system prop that allows defining system overrides as well as additional CSS styles.
   */
  sx: PropTypes.oneOfType([
    PropTypes.arrayOf(
      PropTypes.oneOfType([PropTypes.func, PropTypes.object, PropTypes.bool])
    ),
    PropTypes.func,
    PropTypes.object
  ])
};

export default function JustifyContent2() {
  return (
    <div style={{ width: "100%" }}>
      <Box
        sx={{
          display: "flex",
          justifyContent: "center",
          p: 1,
          m: 1,
          bgcolor: "background.paper",
          borderRadius: 1
        }}
      >
        <Item>
          <Card sx={{ maxWidth: 345 }}>
            <CardActionArea>
              <CardMedia
                component="img"
                height="140"
                image={menu[6]["写真"]}
                alt="green iguana"
              />
              <CardContent>
                <Typography gutterBottom variant="h5" component="div">
                  探索が好き
                </Typography>
                <Typography variant="body2" color="text.secondary">
                  マップがかなり広くやりこみ度がとても高いゲーム。
                  宝箱をとった数に応じて探索度がかわるためやりこみ度が高い。
                </Typography>
              </CardContent>
            </CardActionArea>
          </Card>
        </Item>
        <Item>
          <Card sx={{ maxWidth: 345 }}>
            <CardActionArea>
              <CardMedia
                component="img"
                height="140"
                image={menu[7]["写真"]}
                alt="green iguana"
              />
              <CardContent>
                <Typography gutterBottom variant="h5" component="div">
                  自分のペースで遊べる
                </Typography>
                <Typography variant="body2" color="text.secondary">
                  マルチ要素が少なく、基本ソロプレイとなるため空き時間などに
                  気軽に遊ぶことができたり、自分のペースで遊ぶことができる。
                </Typography>
              </CardContent>
            </CardActionArea>
          </Card>
        </Item>
        <Item>
          <Card sx={{ maxWidth: 345 }}>
            <CardActionArea>
              <CardMedia
                component="img"
                height="140"
                image={menu[8]["写真"]}
                alt="green iguana"
              />
              <CardContent>
                <Typography gutterBottom variant="h5" component="div">
                  豊富なキャラクター
                </Typography>
                <Typography variant="body2" color="text.secondary">
                  国ごとの特徴がある様々なキャラクターがおり、自分の
                  好きなキャラクターを探すことができる。約2カ月毎にキャラクターも増える。
                </Typography>
              </CardContent>
            </CardActionArea>
          </Card>
        </Item>
      </Box>
    </div>
  );
}
