import * as React from "react";
import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";
import CardMedia from "@mui/material/CardMedia";
import Typography from "@mui/material/Typography";
import { CardActionArea } from "@mui/material";
import menu from "./json/gaiyou";

export default function ActionAreaCard2() {
  return (
    <Card sx={{ maxWidth: 450 }}>
      <CardActionArea>
        <CardMedia
          component="img"
          height="140"
          image={menu[6]["写真"]}
          alt="green iguana"
        />
        <CardContent>
          <Typography gutterBottom variant="h5" component="div">
            探索が好き
          </Typography>
          <Typography variant="body2" color="text.secondary">
            マップがかなり広くやりこみ度がとても高いゲーム。
            宝箱をとった数に応じて探索度がかわるためやりこみ度が高い。
          </Typography>
        </CardContent>
      </CardActionArea>
      <Card sx={{ maxWidth: 450 }}>
        <CardActionArea>
          <CardMedia
            component="img"
            height="140"
            image={menu[7]["写真"]}
            alt="green iguana"
          />
          <CardContent>
            <Typography gutterBottom variant="h5" component="div">
              自分のペースで遊べる
            </Typography>
            <Typography variant="body2" color="text.secondary">
              マルチ要素が少なく、基本ソロプレイとなるため空き時間などに
              気軽に遊ぶことができたり、自分のペースで遊ぶことができる。
            </Typography>
          </CardContent>
        </CardActionArea>
        <Card sx={{ maxWidth: 450 }}>
          <CardActionArea>
            <CardMedia
              component="img"
              height="140"
              image={menu[8]["写真"]}
              alt="green iguana"
            />
            <CardContent>
              <Typography gutterBottom variant="h5" component="div">
                豊富なキャラクター
              </Typography>
              <Typography variant="body2" color="text.secondary">
                国ごとの特徴がある様々なキャラクターがおり、自分の
                好きなキャラクターを探すことができる。約2カ月毎にキャラクターも増える。
              </Typography>
            </CardContent>
          </CardActionArea>
        </Card>
      </Card>
    </Card>
  );
}
