import * as React from "react";
import Card from "@mui/material/Card";
import CardActions from "@mui/material/CardActions";
import CardContent from "@mui/material/CardContent";
import CardMedia from "@mui/material/CardMedia";
import Typography from "@mui/material/Typography";
import menu from "./json/gaiyou";

export default function MediaCard() {
  return (
    <Card sx={{ maxWidth: 450 }}>
      <CardMedia
        sx={{ height: 140 }}
        image={menu[3]["写真"]}
        title="green iguana"
      />
      <CardContent>
        <Typography gutterBottom variant="h5" component="div">
          元素
        </Typography>
        <Typography variant="body2" color="text.secondary">
          神の目と呼ばれる7つの元素をもつキャラクターが存在している。
          また、神の目を授かる条件は明かされておらず神の意志は
          関係ないことが言及されている。プレイヤーは、この元素同士の様々な
          反応を用いて戦闘をする。
        </Typography>
      </CardContent>
      <CardActions></CardActions>
      <Card sx={{ maxWidth: 450 }}>
        <CardMedia
          component="img"
          height="140"
          image={menu[4]["写真"]}
          alt="green iguana"
        />
        <CardContent>
          <Typography gutterBottom variant="h5" component="div">
            キャラクター
          </Typography>
          <Typography variant="body2" color="text.secondary">
            原神には、人間以外に仙人、神が存在する。また、7つの国が存在し
            ておりそれぞれ実際の国がモチーフとなっている。その一つに日本がモチーフと
            なっている国もあり、現在4つの国がゲーム内で実装されており、随時実装予定。
          </Typography>
        </CardContent>
        <Card sx={{ maxWidth: 450 }}>
          <CardMedia
            component="img"
            height="140"
            image={menu[5]["写真"]}
            alt="green iguana"
          />
          <CardContent>
            <Typography gutterBottom variant="h5" component="div">
              ストーリー
            </Typography>
            <Typography variant="body2" color="text.secondary">
              生き別れとなった兄(妹)を探すため、様々な国へ行き情報を集めている。
              それぞれの国の神様に会い、兄妹の情報を聞きながら旅をしている。
              その中で新たな出会いがたくさんあり、助け合いをしながら様々な
              国を旅していく。
            </Typography>
          </CardContent>
        </Card>
      </Card>
    </Card>
  );
}
