import * as React from "react";
import PropTypes from "prop-types";
import Box from "@mui/material/Box";
import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";
import CardMedia from "@mui/material/CardMedia";
import Typography from "@mui/material/Typography";
import { CardActionArea } from "@mui/material";
import menu from "./json/gaiyou";

function Item(props) {
  const { sx, ...other } = props;
  return (
    <Box
      sx={{
        p: 1,
        m: 1,
        bgcolor: (theme) =>
          theme.palette.mode === "dark" ? "#101010" : "grey.100",
        color: (theme) =>
          theme.palette.mode === "dark" ? "grey.300" : "grey.800",
        border: "1px solid",
        borderColor: (theme) =>
          theme.palette.mode === "dark" ? "grey.800" : "grey.300",
        borderRadius: 2,
        fontSize: "0.875rem",
        fontWeight: "700",
        ...sx
      }}
      {...other}
    />
  );
}

Item.propTypes = {
  /**
   * The system prop that allows defining system overrides as well as additional CSS styles.
   */
  sx: PropTypes.oneOfType([
    PropTypes.arrayOf(
      PropTypes.oneOfType([PropTypes.func, PropTypes.object, PropTypes.bool])
    ),
    PropTypes.func,
    PropTypes.object
  ])
};

export default function JustifyContent() {
  return (
    <div style={{ width: "100%" }}>
      <Box
        sx={{
          display: "flex",
          justifyContent: "center",
          p: 1,
          m: 1,
          bgcolor: "background.paper",
          borderRadius: 1
        }}
      >
        <Item>
          <Card sx={{ maxWidth: 345 }}>
            <CardActionArea>
              <CardMedia
                component="img"
                height="140"
                image={menu[3]["写真"]}
                alt="green iguana"
              />
              <CardContent>
                <Typography gutterBottom variant="h5" component="div">
                  元素
                </Typography>
                <Typography variant="body2" color="text.secondary">
                  神の目と呼ばれる7つの元素をもつキャラクターが存在している。
                  また、神の目を授かる条件は明かされておらず神の意志は
                  関係ないことが言及されている。プレイヤーは、この元素同士の様々な
                  反応を用いて戦闘をする。
                </Typography>
              </CardContent>
            </CardActionArea>
          </Card>
        </Item>
        <Item>
          <Card sx={{ maxWidth: 345 }}>
            <CardActionArea>
              <CardMedia
                component="img"
                height="140"
                image={menu[4]["写真"]}
                alt="green iguana"
              />
              <CardContent>
                <Typography gutterBottom variant="h5" component="div">
                  キャラクター
                </Typography>
                <Typography variant="body2" color="text.secondary">
                  原神には、人間以外に仙人、神が存在する。また、7つの国が存在し
                  ておりそれぞれ実際の国がモチーフとなっている。その一つに日本がモチーフと
                  なっている国もあり、現在4つの国がゲーム内で実装されており、随時実装予定。
                </Typography>
              </CardContent>
            </CardActionArea>
          </Card>
        </Item>
        <Item>
          <Card sx={{ maxWidth: 345 }}>
            <CardActionArea>
              <CardMedia
                component="img"
                height="140"
                image={menu[5]["写真"]}
                alt="green iguana"
              />
              <CardContent>
                <Typography gutterBottom variant="h5" component="div">
                  ストーリー
                </Typography>
                <Typography variant="body2" color="text.secondary">
                  生き別れとなった兄(妹)を探すため、様々な国へ行き情報を集めている。
                  それぞれの国の神様に会い、兄妹の情報を聞きながら旅をしている。
                  その中で新たな出会いがたくさんあり、助け合いをしながら様々な
                  国を旅していく。
                </Typography>
              </CardContent>
            </CardActionArea>
          </Card>
        </Item>
      </Box>
    </div>
  );
}
