import * as React from "react";
import "./styles.css";
import Tabs from "@mui/material/Tabs";
import Grid from "@mui/material/Grid";
import Tab from "@mui/material/Tab";
import Typography from "@mui/material/Typography";
import Box from "@mui/material/Box";
import ImageAvatars from "./avatar.js";
import Media from "./setumei.js";
import ImageAvatars2 from "./img2.js";
import MediaCard3 from "./card3.js";
import ActionAreaCard2 from "./card4.js";
import BasicButtonGroup from "./button.js";

interface TabPanelProps {
  children?: React.ReactNode;
  index: number;
  value: number;
}

function TabPanel(props: TabPanelProps) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box sx={{ p: 3 }}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

function a11yProps(index: number) {
  return {
    id: `simple-tab-${index}`,
    "aria-controls": `simple-tabpanel-${index}`
  };
}

export default function BasicTabs() {
  const [value, setValue] = React.useState(0);

  const handleChange = (event: React.SyntheticEvent, newValue: number) => {
    setValue(newValue);
  };

  return (
    <Box sx={{ width: "100%" }}>
      <Box sx={{ borderBottom: 1, borderColor: "divider" }}>
        <Tabs
          value={value}
          onChange={handleChange}
          aria-label="basic tabs example"
        >
          <Tab label="ストーリー" {...a11yProps(0)} />
          <Tab label="おすすめ概要" {...a11yProps(1)} />
        </Tabs>
      </Box>
      <TabPanel value={value} index={0}>
        <h4 className="midashi">どのようなゲーム？</h4>
        <div className="box">
          <p>
            の会社mihoyoが手がける新世代オープンワールド型
            ゲーム。PC、スマートフォン、PSなど様々なプラットホームでプレイすることが出来る。
          </p>
        </div>
        <h3>主人公の選択</h3>
        <ImageAvatars />
        <div className="box">
          <p>
            主人公は双子の兄妹。初めに男女どちらかを選択するが、
            それにって大きくストーリーは変わることはない。
          </p>
        </div>
        <h3>ストーリー</h3>
        <ImageAvatars2 />
        <div className="box">
          <p>
            主人公が幻想世界テイワットに訪れた際、謎の神「天理」によって
            襲撃され片割れが連れ去られ、自身は長い間眠りについてしまう。
            目が覚めた時には片割れがおらず、兄（妹）を探す旅が始まる。
          </p>
        </div>
        <h3>世界感</h3>
        <MediaCard3 />
      </TabPanel>
      <TabPanel value={value} index={1}>
        <h4>このような人におすすめ</h4>
        <ActionAreaCard2 />
        <h4>気になったらダウンロード</h4>
        <Grid container alignItems="center" justify="center">
          <Grid item xs={8}>
            <BasicButtonGroup />
          </Grid>
        </Grid>
      </TabPanel>
    </Box>
  );
}
